<template>
	<div>
		<template v-if="loadingResponse">
			<div class="loading">
				<Loader />
			</div>
		</template>
		<template v-else>
			<section class="main-grid__row user-profile">
				<div class="user-profile__card">
					<div class="user-profile__primary">
						<p class="user-profile__name">{{ itemData.response }}</p>
					</div>
					<div class="user-profile__section">
						<h5 class="user-profile__heading">Report Summary</h5>

						<div class="user-profile__item">
							<p v-html="itemData.report"></p>
						</div>

									
						<div class="user-profile__item">
							<button @click="deleteResponse" class="btn btn__danger">Delete</button>
						</div>
					</div>
				</div>
				<div class="user-profile__tabs">
					<ul class="nav nav-tabs" id="myTab" role="tablist">
						<li class="nav-item">
							<a class="nav-link active" id="profile-tab" data-toggle="tab" href="#profile" role="tab" aria-controls="profile" aria-selected="true">Edit Profile</a>
						</li>
					</ul>
					<div class="tab-content" id="myTabContent">
						<div class="tab-pane fade show active user-profile__tabs__section" id="profile" role="tabpanel" aria-labelledby="profile-tab">
							<div class="settings">
								<form
									method="post"
									@submit.prevent="updateResponse">
									<div class="settings__primary">
										<button
											class="btn btn__save"
											type="button"
											name="button"
											@click.prevent="updateResponse"
											v-bind="{'disabled': loading}">
											Save
										</button>
									</div>
									<div class="invalid-feedback">
										{{ errors.avatar }}
									</div>
									<div class="settings__field">
										<div class="form-group">
											<div class="settings__value">
												<label for="userFirstname">Response</label>
												<input
													:class="[{'is-invalid': errors.response}]"
													@keydown="clearErrors($event.target.name)"
													v-model="itemData.response"
													name="fname"
													value=""
													type="text"
													class="form-control"
													id="userFirstname">
											</div>
											<div class="invalid-feedback">
												{{ errors.response }}
											</div>
										</div>
										<div class="form-group">
											<div class="settings__value">
												<label for="userLastname">Report</label>
												<quill-editor ref="myTextEditor"
													v-model="itemData.report"
													:options="editorOption">
												</quill-editor>
											</div>
											<div class="invalid-feedback">
												{{ errors.report }}
											</div>
										</div>
									</div>
								</form>
							</div>
						</div>
					</div>
				</div>
			</section>
		</template>
	</div>
</template>

<script>
import 'quill/dist/quill.core.css';
import 'quill/dist/quill.snow.css';
import 'quill/dist/quill.bubble.css';
import { quillEditor } from 'vue-quill-editor';
import { setTimeout } from 'timers';
import Loader from '../../components/Loader';
import form from '../../mixins/form';
import quiz from '../../utils/validations/quiz';
import { updateTestResponse, getTestResponse, deleteTestResponse } from '../../utils/api/quiz';
import { success, fail } from '../../utils/toast';

export default {
	name: 'TestResponseDetail',
	mixins: [form],
	data() {
		return {
			itemData: {},
			loading: false,
			errors: {},
			loadingResponse: true,
			responseLoaded: false,
			editorOption: {
				modules: {
					toolbar: [
						['bold', 'italic', 'underline', 'strike'],
						['blockquote', 'code-block'],
						[{ 'header': 1 }, { 'header': 2 }],
						[{ 'list': 'ordered' }, { 'list': 'bullet' }],
						[{ 'script': 'sub' }, { 'script': 'super' }],
						[{ 'indent': '-1' }, { 'indent': '+1' }],
						[{ 'direction': 'rtl' }],
						[{ 'size': ['small', false, 'large', 'huge'] }],
						[{ 'header': [1, 2, 3, 4, 5, 6, false] }],
						[{ 'font': [] }],
						[{ 'color': [] }, { 'background': [] }],
						[{ 'align': [] }],
						['clean'],
						['link', 'image', 'video']
					]
				}
			}
		};
	},
	created() {
		this.getResponse();
	},
	methods: {
		getResponse() {
			getTestResponse(this.$route.params.id)
				.then((response) => {
					if (response.status === 200) {
						this.responseLoaded = true;
						this.itemData = response.data.data;
						return true;
					}
					this.loadingResponse = false;
					return false;
				})
				.catch((error) => {
					return error;
				})
				.finally(() => {
					this.loadingResponse = false;
				});
			return true;
		},
		updateResponse() {
			const result = this.itemData;
			['created_at', 'updated_at', 'id'].forEach((key) => {
				delete result[key];
			});
			this.validate(result, quiz.updateResponse);
			if (Object.keys(this.issues).length) {
				this.errors = this.issues;
				return false;
			}
			this.loading = true;
			updateTestResponse(this.$route.params.id, this.itemData)
				.then((response) => {
					if (response.status === 200) {
						this.loading = false;
						success({ title: 'Success! Response updated' });
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		},
		deleteResponse() {
			deleteTestResponse(this.$route.params.id)
				.then((response) => {
					if (response.status === 200) {
						success({ title: 'Success! Response deleted' });
						setTimeout(() => {
							this.$router.push({ path: '/response' });
						}, 2000);
					}
					return true;
				})
				.catch((error) => {
					fail({ title: 'Oops! Something went wrong' });
					return error;
				});
			return true;
		}
	},
	components: {
		Loader,
		quillEditor
	},
	computed: {
		editor() {
			return this.$refs.myTextEditor.quill;
		}
	}
};
</script>

